* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #242121;
}
.Navbar {
  background-color: #292828;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 1rem;
  padding-right: 1rem;
  height: 10vh;
}
.Logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.Logo img {
  height: 8vh;
}
.Logo p {
  font-size: 1.3rem;
  color: White
}
.Logo span {
  color: rgb(211, 200, 200);
  font-size: 0.8rem;
}
.Search {
  display: flex;
  gap: 1rem;
}
.Search input {
  width: 40vw;
  padding: 0.4rem;
  background-color: rgb(87, 85, 85);
  border: 1px solid white;
}
.Search button {
  padding: 0.4rem 1.5rem;
  background-color:  rgb(73, 71, 71);
  border: 1px solid white;
  color: white;
}
.Menu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.Menu img {
  height: 1.8rem;
}
.Book-Section {
  display: flex;
  flex-direction: column;
  padding: 0rem 4rem;
  gap: 2rem;
}
.Book-Section h2 {
  color: white;
  padding-top: 2rem;
}
.HarryBooks {
  display: flex;
  gap: 5rem;
  overflow: hidden;
  cursor: pointer;
}
.sherlockBooks {
  display: flex;
  gap: 5rem;
  overflow: hidden;
  cursor: pointer;
}